&__alert {
	position: relative;
	display: flex;
	align-items: center;
	width: 100%;
	height: 80px;
	margin-bottom: 15px;
	border: 3px solid #2c557e;
	border-radius: 7px;
	background-color: #cde8fb;
	@include transition(box-shadow);

	&:hover {
		box-shadow: 0 0 10px 0 rgba(0,0,0,.3);
		@include transition(box-shadow);
	}

	&-close {
		position: absolute;
		top: 10px;
		right: 10px;

		.icon-font {
			color: #2c557e;
			font-size: 10px;
		}
	}

	> div {
		position: relative;

		&:nth-of-type(1) {
			width: 120px;
		}
		&:nth-of-type(2) {
			flex: 1;
			min-width: 1px;
			max-width: 100%;
		}

		.icon-font {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);

			&:nth-of-type(1) {
				color: #ffffff;
				font-size: 75px;
			}
			&:nth-of-type(2) {
				color: #2c557e;
				font-size: 55px;
			}
		}

		p {
			padding-bottom: 5px;
			color: #2c557e;
			font-weight: 500;
			font-size: 16px;
		}

		form {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			width: 100%;

			label {
				display: flex;
				align-items: center;
				justify-content: flex-start;
				width: 100%;
				max-width: 350px;
				padding-right: 10px;
			}

			input {
				outline: none;
				width: 100%;
				height: 35px;
				padding: 0 12px;
				border-radius: 5px;
				border: 1px solid #ffffff;
				box-shadow: 0 4px 4px 0 rgba(0,0,0,.1);
				background-color: #ffffff;
				color: #666666;
				font-size: 14px;
			}
		}

		.c-btn {
			min-width: unset;
			height: 35px;
		}
	}
}
